@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

:root {
  --color: red;
}

body {
  background: var(--color);
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.container {
  width: 200px;
  height: 250px;
  background-color: #fff;
  border-radius: 30px;
}

.analog {
  height: 200px;
  width: 200px;
  background: #fff;
  border-radius: 50%;
  position: relative;
  top: -30%;
  box-shadow: 1px 1px 10px #0000004d, -1px -1px 10px #0000004d;
}

.analog p {
  text-align: center;
  transform: rotate(calc(var(--i) * (360deg / 12)));
  position: absolute;
  inset: 8px;
}

.analog p span {
  color: var(--color);
  transform: rotate(calc(var(--i) * (-360deg / 12)));
  font-size: 18px;
  font-weight: 500;
  display: inline-block;
}

.hands {
  height: 10px;
  width: 10px;
}

.hands:before {
  content: "";
  height: 12px;
  width: 12px;
  z-index: 100;
  background: var(--color);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hand {
  height: 6px;
  background: var(--color);
  transform-origin: 100%;
  border-radius: 6px;
  transition-timing-function: cubic-bezier(.1, 2.7, .58, 1);
  position: absolute;
  top: 50%;
  right: 50%;
  transform: rotate(90deg);
}

.hand.hour-hand {
  width: 20%;
  height: 4.2px;
  z-index: 1;
}

.hand.minute-hand {
  width: 30%;
  height: 2.5px;
  z-index: 2;
}

.hand.second-hand {
  width: 40%;
  height: 1.5px;
  background: var(--color);
}

.date {
  text-align: center;
  color: var(--color);
  margin-top: -50px;
  margin-bottom: 15px;
  font-size: 15px;
  font-weight: 500;
}

.digital {
  color: #fff;
  background-color: var(--color);
  height: 50px;
  width: 90%;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  margin: auto;
  font-weight: 500;
  display: flex;
}

.time {
  width: 80%;
  font-size: 20px;
}

.ampm {
  width: 20%;
  margin-left: -30px;
  margin-right: -10px;
}

.colors {
  background-color: #fff;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 10px;
  display: flex;
}

.color {
  width: 40px;
  height: 40px;
  cursor: pointer;
  border: 2px solid #fff;
  border-radius: 50%;
}

.redC {
  background-color: red;
}

.grayC {
  background-color: gray;
}

.slateblueC {
  background-color: #6a5acd;
}

.greenC {
  background-color: green;
}

.blueC {
  background-color: #00f;
}

.blackC {
  background-color: #000;
}

